<template>
  <div>
    <ul class="tag" v-if="showTag">
      <router-link to="/views/collegePersonnel/scientificResearchPersonnel">
        <li :class="{ active: state == 1 }" @click="selectTag(1)">科研人员</li>
      </router-link>

      <router-link to="/views/collegePersonnel/talentPlan">
        <li :class="{ active: state == 2 }" @click="selectTag(2)">人才计划</li>
      </router-link>

      <router-link to="/views/collegePersonnel/academicCommittee">
        <li :class="{ active: state == 3 }" @click="selectTag(3)">学术委员会</li>
      </router-link>

      <router-link to="/views/collegePersonnel/ethicsCommittee">
        <li :class="{ active: state == 4 }" @click="selectTag(4)">伦理委员会</li>
      </router-link>
    </ul>
    <div class="tagNull" v-if="showTag"></div>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'collegePersonnel',
  data () {
    return {
      state: 1,
      tagsUrl: [
        '/views/collegePersonnel/scientificResearchPersonnel',
        '/views/collegePersonnel/talentPlan',
        '/views/collegePersonnel/academicCommittee',
        '/views/collegePersonnel/ethicsCommittee'
      ],
      showTag: false
    }
  },
  methods: {
    selectTag (state) {
      if (state == this.state) {
        return
      }
      this.state = state
    }
  },
  created () {
    const arrIndex = $.inArray(this.$route.path, this.tagsUrl)
    if (arrIndex > -1) {
      this.showTag = true
      this.state = arrIndex + 1
    }
  }

}
</script>



